import React, { useRef } from 'react'
import { useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { API_ROLE, API_ROLE_GET, API_USERS, ROLE_LIST } from '../links';
import { api } from "../api";
import { useEffect } from 'react';
import { MultiSelect } from 'primereact/multiselect';
import "primereact/resources/themes/lara-light-cyan/theme.css";
import { Toast } from 'primereact/toast';

export default ({ isNewRecord }) => {
    const toast = useRef(null);
    const [role, setRole] = useState({ name: "", users: [] });
    const [users, setUsers] = useState([]);
    const [error, setError] = useState({});
    const history = useNavigate();
    const { id } = useParams()
    const add = (e) => {
        e.preventDefault();
        if (role.name === "") {
            setError({ name: "Role name is required" });
            return;
        }
        if (role.users.length === 0) {
            setError({ name: "User field cannot be empty" });
            return;
        }
        console.log(role, "this is name")
        if (isNewRecord) {
            api.post(API_ROLE, role)
                .then(e => {
                    toast.current.show({ severity: 'info', summary: 'Success', detail: 'Record Added', life: 3000 });
                    history(ROLE_LIST)
                });
        } else {
            api.put(API_ROLE + "/" + id, role)
                .then(e => {
                    toast.current.show({ severity: 'info', summary: 'Success', detail: 'Record Updated', life: 3000 });
                    history(ROLE_LIST)
                });
        }

    }

    useEffect(() => {
        if (id) {
            api.get(API_ROLE_GET.replace(":id", id))
                .then(e => {
                    setRole({ ...e.data });
                })
        }

        api.get(API_USERS)
            .then(e => {
                const user = e.data.map(e => {
                    return { label: e.name, value: e.id };
                })
                console.log(user)
                setUsers(user);
            })
    }, [])

    return (
        <div className="card">
            <div className="card-header">
                <h5 className="card-title">{isNewRecord ? "Add" : "Update"} Role</h5>
                <Link to={ROLE_LIST}>
                    <button className="btn btn-primary" style={{ float: "right" }}>Back</button>
                </Link>
            </div>
            <div className='card-body'>
                <form onSubmit={add} className="row g-3">
                    <div className="mb-3 col-md-4">
                        <label className='form-label'>Name</label>
                        <input
                            type="text"
                            name="name"
                            placeholder="Enter Role Name"
                            className='form-control'
                            value={role.name}
                            onChange={(e) => {
                                setRole(p => {
                                    return { ...p, name: e.target.value }
                                })
                            }}
                        />
                        {error.name && (
                            <div class="invalid-feedback">{error.name}</div>
                        )}
                    </div>
                    <div className="mb-3 col-md-8">
                        <label className='form-label'>Users</label>
                        <br />
                        <MultiSelect
                            value={role.users}
                            onChange={(e) => {
                                console.log(e)
                                setRole(p => {
                                    return { ...p, users: e.value }
                                })
                            }}
                            options={users}
                            optionLabel="label"
                            display="chip"
                            placeholder="Select Users"
                            className="" />
                        {error.user && (
                            <div class="invalid-feedback">{error.user}</div>
                        )}
                    </div>
                    <div className="col-12">
                        <button className={"text-white btn btn-" + (isNewRecord ? "primary" : "success")}>{isNewRecord ? "Add" : "Update"}</button>
                    </div>
                </form>
            </div>
            <Toast ref={toast} />
        </div>
    );

}