import { useRef, useState } from "react"
import { API_ROLE, API_ROLE_GET, ROLE_ACCESS, ROLE_ADD, ROLE_UPDATE } from "../links";
import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom"
import { api } from "../api";
import { ConfirmDialog, confirmDialog } from 'primereact/confirmdialog';
import { Toast } from 'primereact/toast';

export default () => {
    const toast = useRef(null);
    const [roles, setRoles] = useState([]);
    const history = useNavigate()
    const getList = () => {
        api.get(API_ROLE)
            .then(e => setRoles(e.data));
    }
    useEffect(() => {
        console.log("app effect get");
        getList()
        return () => setRoles([])
    }, []);

    const confirm = (role) => {
        confirmDialog({
            message: `Do you want to delete '${role.name}'?`,
            header: 'Delete Confirmation',
            icon: 'pi pi-info-circle',
            defaultFocus: 'reject',
            acceptClassName: 'p-button-danger',
            accept: () => {
                deleteConfirm(role.id)
            }
        });
    };
    const deleteConfirm = (id) => {

        api.delete(API_ROLE_GET.replace(":id", id))
            .then(e => {
                getList();
                toast.current.show({ severity: 'info', summary: 'Confirmed', detail: 'Role deleted', life: 3000 });
            });
        return false
    }

    return (
        <div className="card">
            <div className="card-header">
                <h5 className="card-title">Role List</h5>
                <Link to={ROLE_ADD}>
                    <button className="btn btn-primary" style={{ float: "right" }}>Add Role</button>
                </Link>
            </div>
            <table className="table">
                <thead>
                    <tr>
                        <th style={{ width: "10%" }}>#</th>
                        <th style={{ width: "25%" }}>Name</th>
                        <th style={{ width: "50%" }}>Users</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {roles.length == 0 && (<tr><td colSpan={4}>No record found</td></tr>)}
                    {roles.length && roles.map((e, i) => {
                        return <tr key={e.id}>
                            <td>{i + 1}</td>
                            <td>{e.name}</td>
                            <td>{e.users?.map(e => {
                                return <span key={e.id}>{e.name}</span>
                            })}</td>
                            <td className="table-action">
                                <a href="javascript:void(0)" onClick={() => { history(ROLE_UPDATE.replace(":id", e.id)) }}><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-edit-2 align-middle"><path d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"></path></svg></a>
                                <a href="javascript:void(0)" onClick={() => { history(ROLE_ACCESS.replace(":id", e.id)) }}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 54 45" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-edit-2 align-middle mx-3">
                                        <path d="M 34 0 C 25.179688 0 18 7.175781 18 16 C 18 17.960938 18.382813 19.824219 19.03125 21.5625 L 0.28125 40.28125 L 0 40.59375 L 0 47.40625 L 0.28125 47.71875 L 2.28125 49.71875 L 2.59375 50 L 9.40625 50 L 9.71875 49.71875 L 12.71875 46.71875 L 13 46.40625 L 13 44 L 15.40625 44 L 15.71875 43.71875 L 18.71875 40.71875 L 19 40.40625 L 19 39 L 20.40625 39 L 20.71875 38.71875 L 22.71875 36.71875 L 23 36.40625 L 23 35 L 24.40625 35 L 24.71875 34.71875 L 28.4375 30.96875 C 30.175781 31.617188 32.039063 32 34 32 C 42.820313 32 50 24.820313 50 16 C 50 7.175781 42.820313 0 34 0 Z M 34 2 C 41.738281 2 48 8.257813 48 16 C 48 23.738281 41.738281 30 34 30 C 32.078125 30 30.257813 29.636719 28.59375 28.9375 C 28.582031 28.925781 28.574219 28.917969 28.5625 28.90625 C 23.535156 26.78125 20 21.804688 20 16 C 20 8.257813 26.261719 2 34 2 Z M 34 5 C 31.183594 5 28.363281 6.074219 26.21875 8.21875 L 25.5 8.9375 L 26.21875 9.625 L 40.375 23.78125 L 41.0625 24.5 L 41.78125 23.78125 C 46.070313 19.496094 46.070313 12.503906 41.78125 8.21875 C 39.636719 6.074219 36.816406 5 34 5 Z M 34 7 C 36.300781 7 38.613281 7.863281 40.375 9.625 C 43.648438 12.898438 43.75 17.996094 40.9375 21.53125 L 28.46875 9.0625 C 30.101563 7.765625 32.023438 7 34 7 Z M 19.875 23.53125 C 21.371094 26.328125 23.671875 28.628906 26.46875 30.125 L 23.5625 33 L 21 33 L 21 35.5625 L 19.5625 37 L 17 37 L 17 39.5625 L 14.5625 42 L 11 42 L 11 45.5625 L 8.5625 48 L 3.4375 48 L 2.4375 47 L 19 30.4375 C 19.359375 30.128906 19.457031 29.613281 19.230469 29.199219 C 19.003906 28.78125 18.515625 28.582031 18.0625 28.71875 C 17.871094 28.761719 17.699219 28.859375 17.5625 29 L 2 44.59375 L 2 41.4375 Z"></path>
                                    </svg>
                                </a>
                                <a href="javascript:void(0)" onClick={() => { confirm({ id: e.id, name: e.name }) }}><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-trash align-middle"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path></svg></a>
                            </td>
                        </tr>
                    })}
                </tbody>
            </table>
            <Toast ref={toast} />
            <ConfirmDialog />
        </div>
    )
}