import { useRef, useState } from "react"
import { API_ACCESS, API_ROLE_ACCESS, API_ROLE_ACCESS_GET, API_ROLE_ACCESS_ID, ROLE_LIST } from "../links";
import { useEffect } from "react";
import { Link, useParams } from "react-router-dom"
import { api } from "../api";
import { Toast } from 'primereact/toast';

export default () => {
    const toast = useRef(null);
    const [accessList, setAccess] = useState([]);
    const { id } = useParams()
    const getList = () => {
        api.get(API_ACCESS)
            .then(e => {
                return api.get(API_ROLE_ACCESS_ID.replace(":id", id))
                    .then(a => {
                        return e.data.map(aa => {
                            return {
                                ...aa,
                                status: a.data.some(f => f.access_id == aa.id),
                                role_access: a.data.find(f => f.access_id == aa.id)?.id
                            }
                        })
                    })
            })
            .then(e => {
                console.log(e)
                setAccess(e)
            }).catch(reason => {

            });
    }
    useEffect(() => {
        getList()
        return () => setAccess([])
    }, []);

    const switchAccess = (access) => {
        if (access.status) {
            api.delete(API_ROLE_ACCESS_GET.replace(":id", access.role_access))
                .then(e => {
                    getList();
                    toast.current.show({ severity: 'warning', summary: 'Success', detail: 'Access Denied', life: 3000 });
                });
        } else {
            api.post(API_ROLE_ACCESS, { role_id: id, access_id: access.id })
                .then(e => {
                    getList();
                    toast.current.show({ severity: 'info', summary: 'Success', detail: 'Access Granted', life: 3000 });
                });
        }
        return false
    }

    return (
        <div className="card">
            <div className="card-header">
                <h5 className="card-title">Access List</h5>
                <Link to={ROLE_LIST}>
                    <button className="btn btn-primary" style={{ float: "right" }}>Back</button>
                </Link>
            </div>
            <table className="table">
                <thead>
                    <tr>
                        <th style={{ width: "10%" }}>#</th>
                        <th style={{ width: "25%" }}>Name</th>
                        <th style={{ width: "25%" }}>Method</th>
                        <th style={{ width: "25%" }}>Path</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {accessList.length == 0 && (<tr><td colSpan={5}>No record found</td></tr>)}
                    {accessList.length && accessList.map((e, i) => {
                        return <tr key={e.id}>
                            <td>{i + 1}</td>
                            <td>{e.name}</td>
                            <td></td>
                            <td></td>
                            <td className="table-action">
                                <div className="form-check form-switch">
                                    <input className="form-check-input"
                                        type="checkbox"
                                        role="switch"
                                        id="flexSwitchCheckChecked"
                                        checked={e.status}
                                        onClick={() => {
                                            switchAccess(e)
                                        }} />
                                    {/* <label className="form-check-label" for="flexSwitchCheckChecked"></label> */}
                                </div>
                            </td>
                        </tr>
                    })}
                </tbody>
            </table>
            <Toast ref={toast} />
        </div>
    )
}